@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700;900&display=swap");

body {
  margin: 0;
  font-family: "Source Sans Pro", Helvetica, sans-serif;
}
.navbar {
  width: calc(100% - 48px);
  height: 56px;
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: #000000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 24px;
  color: #ffffff;
  z-index: 100;
}
.navbar-logo {
  font-size: 28px;
  font-weight: 900;
  cursor: pointer;
}
.navbar-subtitle {
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
}
